import axios from "axios"
export default{
    async getCoursesByPlanId(params){
        let result = await axios({
            method: 'get',
            url:  $baseUrl + '/courses/getCoursesByPlanId',
            params: params
        });
        return result?.data?.data;
    },
    async createCourse(data){
        let result = await axios({
            method: 'post',
            url:  $baseUrl + '/courses/createCourse',
            data: data
        });
        return result?.data?.data;
    },
    async editCourse(data){
        let result = await axios({
            method: 'post',
            url:  $baseUrl + '/courses/editCourse',
            data: data
        });
        return result?.data?.data;
    },
    async deleteCourse(data){
        let result = await axios({
            method: 'post',
            url:  $baseUrl + '/courses/deleteCourse',
            data: data
        });
        return result?.data?.data;
    },
}