<template>
    <div class="homeOuter">
        <n-h3>当前应用：{{ apps[router.currentRoute.value.query.app].name }}</n-h3>
        <n-h1 style="margin-top: 0;">导入一张 学生成绩表 来开始统计</n-h1>
        <div>
            <n-upload :action="baseUrl + '/tasks/importByExcel'" style="width: 60vw;" @before-upload="beforeUpload"
                :on-finish="onFinishUpload" :show-file-list="false" :on-error="onUploadError">
                <n-upload-dragger>
                    <div style="margin-bottom: 12px">
                        <n-icon size="48" :depth="3">
                            <archive-icon />
                        </n-icon>
                    </div>
                    <n-text style="font-size: 16px">
                        点击或者拖动文件到该区域来导入
                    </n-text>
                    <n-p depth="3" style="margin: 8px 0 0 0">
                        导入的文件应符合格式要求，至少包含以下几列：学号、学期、课程名称、课程代码、课程名称、成绩、学分。
                    </n-p>
                </n-upload-dragger>
            </n-upload>
            <n-button size="small" secondary strong style="margin-top: 10px; float: right;" @click="downloadTemplate">
                点击此处下载模板文件
            </n-button>
        </div>
        <n-modal v-model:show="showStartTaskModal" preset="card" style="width: 40vw;" :mask-closable="false">
            <template #header>
                <div>选择教学计划方案</div>
            </template>
            <n-form-item label="教学计划方案">
                <n-select v-model:value="selectedPlanId" :options="plans" label-field="planName"
                    value-field="planId" style="min-width: 200px;" placeholder="选择要使用的教学计划方案" />
            </n-form-item>
            <n-button type="primary" @click="handleStartTask()" style="margin-top: 10px;">
                确定
            </n-button>
        </n-modal>
    </div>
</template>

<script setup>
import { Add, ArchiveOutline as ArchiveIcon } from '@vicons/ionicons5'
import { ref, onMounted } from "vue"
import { useRouter } from "vue-router"
import PlanApi from "@/apis/PlanApi"
import CourseApi from "@/apis/CourseApi"
import { useMessage } from "naive-ui"
import moment from "moment";

const apps = {
    creditStatistic: {
        name: "教学方案完成情况统计"
    },
    gradeStatistic: {
        name: "必修课平均学分成绩统计"
    }
}

window.$message = useMessage();

const router = useRouter();

let taskId = -1;

const baseUrl = ref("");

const plans = ref([]);

onMounted(async () => {
    baseUrl.value = $baseUrl;
    plans.value = await PlanApi.getAllPlans();
})


const beforeUpload = async (data) => {
    if (data.file.name.indexOf(".xlsx") == -1 && data.file.name.indexOf(".xls") == -1) {
        window.$message.error("只能上传Excel表格文件，请重新上传");
        return false;
    }
    return true;
}

const onFinishUpload = async (data) => {
    taskId = JSON.parse(data.event.currentTarget.response).data.taskId;
    showStartTaskModal.value = true;
    window.$message.success("导入成功");
}


const onUploadError = async (data) => {
    window.$message.error(JSON.parse(data.event.currentTarget.response).data);
}

const showStartTaskModal = ref(false);
const selectedPlanId = ref();

const handleStartTask = () => {
    if(router.currentRoute.value.query.app == "creditStatistic"){
        router.push({
            name: "result",
            query: {
                planId: selectedPlanId.value,
                taskId
            }
        })
    } else if(router.currentRoute.value.query.app == "gradeStatistic"){
        router.push({
            name: "gradeResult",
            query: {
                planId: selectedPlanId.value,
                taskId
            }
        })
    }
}

const downloadTemplate = () =>{
    window.open($baseUrl + "/学生成绩模板文件.xlsx")
}

</script>


<style scoped lang="scss">
.homeOuter {
    height: calc(100vh - 63px) !important;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #F1F1F1;
    flex-direction: column;

}
</style>