<template>
    <div class="resultOuter">
        <div style="width: 100vw; height:calc(100vh - 97px); display: flex;">
            <n-layout-sider bordered collapse-mode="width" :collapsed-width="12" :width="130" :collapsed="menuCollapsed"
                show-trigger @collapse="menuCollapsed = true" @expand="menuCollapsed = false"
                :show-collapsed-content="false">
                <n-menu v-if="studentList.length > 0" :collapsed="menuCollapsed" :collapsed-width="64"
                    :collapsed-icon-size="22" :options="studentList" key-field="studentId" label-field="studentName"
                    :on-update:value="handleMenuChange" :default-value="studentList[0].studentId" />
            </n-layout-sider>
            <div class="content" style="width: 100%; height:calc(100vh - 97px); overflow: auto;">
                <n-page-header v-if="currentData.studentName != undefined" style="margin: 20px;"
                    :subtitle="currentStudentId">
                    <template #title>
                        {{ currentData?.studentName }}
                    </template>

                    <template #extra>
                        <n-space>
                            <div style="display: flex; align-items: center;">
                                重复选修时 <n-radio-group v-model:value="repeatMode" name="radiobuttongroup1"
                                    style="margin-left: 5px;" :on-update:value="handleChangeRepeatMode">
                                    <n-radio-button v-for="item in ['取最高成绩', '取最初成绩']" :key="item" :value="item"
                                        :label="item" />
                                </n-radio-group>
                            </div>
                            <!-- <n-button size="large" type="info" @click="handleStartEditPlan">重命名</n-button> -->

                        </n-space>
                    </template>
                    <n-card v-for="(value, key) in currentData.courseData" :title="key" hoverable
                        style="margin-bottom: 20px;">
                        <template #header-extra>
                            <span v-if="key =='专业选修课程'" :style="{ color: getProfElectiveCategoryDesc(value).passed ? '#18A058' : '#D03050' }">
                                {{ getProfElectiveCategoryDesc(value).text }}
                            </span>
                            <span v-else-if="key =='通识教育选修课'" :style="{ color: getElectiveCategoryDesc(value).passed ? '#18A058' : '#D03050' }">
                                {{ getElectiveCategoryDesc(value).text }}
                            </span>
                            <span v-else :style="{ color: getNecessaryCategoryDesc(value).passed ? '#18A058' : '#D03050' }">
                                {{ getNecessaryCategoryDesc(value).text }}
                            </span>
                            <n-button quaternary @click="value.isExpanded = true" v-show="!value.isExpanded">展开</n-button>
                            <n-button quaternary @click="value.isExpanded = false" v-show="value.isExpanded">收起</n-button>
                        </template>
                        <n-table :bordered="false" :single-line="false" size="small" v-show="value.isExpanded">
                            <thead>
                                <tr>
                                    <th width="100px">课程代码</th>
                                    <th>课程名称</th>
                                    <th width="100px">学分</th>
                                    <th width="200px">修读学期</th>
                                    <th width="100px">得分</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="planCourse in value">
                                    <td>{{ planCourse.courseCode }}</td>
                                    <td>{{ planCourse.courseName }}</td>
                                    <td>{{ planCourse.courseCredit }}</td>
                                    <td>{{ planCourse?.grade?.semester }}</td>
                                    <td
                                        :style="{ backgroundColor: compareGrade(planCourse?.grade, { courseMark: 60 }) >= 0 ? 'white' : '#ff000055' }">
                                        {{ planCourse?.grade?.courseMark }}
                                    </td>
                                </tr>
                            </tbody>
                        </n-table>
                    </n-card>
                </n-page-header>
            </div>
        </div>
        <n-layout-footer bordered style="padding: 5px 10px; display: flex; justify-content: space-between;">
            <div>共计 {{ studentList.length }} 名学生</div>
            <div>使用教学计划方案： {{ plan.planName }}</div>
        </n-layout-footer>
    </div>
</template>

<script setup>
import { Add, ArchiveOutline as ArchiveIcon } from '@vicons/ionicons5'
import { ref, onMounted } from "vue"
import { useRouter } from "vue-router"
import PlanApi from "@/apis/PlanApi"
import CourseApi from "@/apis/CourseApi"
import TaskApi from '@/apis/TaskApi'
import { useMessage } from "naive-ui"
import moment from "moment";

window.$message = useMessage();

const router = useRouter();

const menuCollapsed = ref(false);

const baseUrl = ref("");

const planId = ref();

const studentList = ref([]);

const plan = ref({});

let planCourses = [];

onMounted(async () => {
    baseUrl.value = $baseUrl;
    planId.value = router.currentRoute.value.query.planId;
    studentList.value = await TaskApi.getStudentsList({taskId: router.currentRoute.value.query.taskId});
    plan.value = await PlanApi.getPlanById({ planId: router.currentRoute.value.query.planId })
    plan.value.planConfigs = JSON.parse(plan.value.planConfigs);
    if(plan.value.planConfigs.directions == undefined || plan.value.planConfigs.electiveCredits == undefined || plan.value.planConfigs.profElectiveCredits == undefined){
        alert("教学计划方案信息不完整，请确认你填写了毕业要求学分和方向课信息。")
        router.back();
    }
    await getCoursesData();
    currentStudentId.value = studentList.value[0].studentId;
    await getData();
})


const getCoursesData = async () => {
    planCourses = await CourseApi.getCoursesByPlanId({
        planId: router.currentRoute.value.query.planId
    });

}

const currentStudentId = ref(-1);

const handleMenuChange = (ev) => {
    currentStudentId.value = ev;
    getData();
}

const currentData = ref({});

const repeatMode = ref("取最高成绩");

const getData = async () => {
    let studentId = currentStudentId.value;
    let courseData = await TaskApi.getDataByStudentId({ studentId, taskId: router.currentRoute.value.query.taskId });
    // 形成最终的渲染数据
    let courseDictByCategory = {};
    let electiveCourses = [];
    for (let planCourse of planCourses) {
        if (courseDictByCategory[planCourse.courseCategory] == undefined) {
            courseDictByCategory[planCourse.courseCategory] = []
        }
        courseDictByCategory[planCourse.courseCategory].push(planCourse);
        let studentGrades = [];
        for (let grade of courseData) {
            if (grade.courseCode == planCourse.courseCode) {
                studentGrades.push(grade);
                grade.hasCategoried = true;
            }
        }
        planCourse.grade = getFinalGrade(studentGrades);
    }
    courseDictByCategory["通识教育选修课"] = []
    for (let grade of courseData) {
        if (grade.hasCategoried == undefined) {
            electiveCourses.push(grade);
        }
    }

    for (let course of courseData) {
        if (course.hasCategoried == undefined) {
            courseDictByCategory["通识教育选修课"].push(course);
            course.grade = JSON.parse(JSON.stringify(course));
        }
    }

    // console.log(courseDictByCategory);
    currentData.value = {
        studentId: courseData[0].studentId,
        studentName: courseData[0].studentName,
        courseData: courseDictByCategory
    }
}

const compareGrade = (a, b) => {
    if (a == undefined) a = { courseMark: 0 };
    if (b == undefined) b = { courseMark: 0 };
    if (a.courseMark == "未考" || a.courseMark == "旷考" || a.courseMark == "不及格") {
        a.courseMark = 0;
    }
    if (b.courseMark == "未考" || b.courseMark == "旷考" || b.courseMark == "不及格") {
        b.courseMark = 0;
    }
    if (a.courseMark == "及格") a.courseMark = 60;
    if (b.courseMark == "及格") b.courseMark = 60;
    if (a.courseMark == "中") a.courseMark = 70;
    if (b.courseMark == "中") b.courseMark = 70;
    if (a.courseMark == "良") a.courseMark = 80;
    if (b.courseMark == "良") b.courseMark = 80;
    if (a.courseMark == "优") a.courseMark = 90;
    if (b.courseMark == "优") b.courseMark = 90;
    return Number(a.courseMark) - Number(b.courseMark)
}

const getFinalGrade = (studentGrades) => {
    if (repeatMode.value == "取最初成绩") {
        studentGrades.sort((a, b) => {
            return (a.semester > b.semester) ? 1 : -1;
        })
        return studentGrades[0];
    } else if (repeatMode.value == "取最高成绩") {
        studentGrades.sort(compareGrade);
        return studentGrades[studentGrades.length - 1];
    }
}

const handleChangeRepeatMode = (ev) => {
    repeatMode.value = ev;
    console.log(repeatMode.value);
    getData();
}

const getNecessaryCategoryDesc = (courses) => {
    let totalPlanCredits = 0;
    let totalGotCredits = 0;
    for(let item of courses){
        totalPlanCredits += Number(item.courseCredit);
        if(compareGrade(item?.grade, { courseMark: 60 }) >= 0) {
            totalGotCredits += Number(item.courseCredit);
        }
    }
    return {
        text: `学分数${totalGotCredits}/${totalPlanCredits}`,
        passed: totalGotCredits >= totalPlanCredits
    }
}

const getProfElectiveCategoryDesc = (courses) => {
    let totalPlanCredits = plan.value.planConfigs.profElectiveCredits;
    let totalGotCredits = 0;
    let directionInfo = JSON.parse(JSON.stringify(plan.value.planConfigs.directions));
    for(let direction of directionInfo){
        direction.passedCount = 0;
    }
    for(let item of courses){
        if(compareGrade(item?.grade, { courseMark: 60 }) >= 0) {
            totalGotCredits += Number(item.courseCredit);
            for(let direction of directionInfo){
                for(let courseCode of direction.courses){
                    if(courseCode == item.courseCode){
                        direction.passedCount ++;
                    }
                }
                console.log(item);
            }
        }
    }
    let passedDirections = [];
    for(let direction of directionInfo){
        if(direction.passedCount == direction.courses.length){
            passedDirections.push(direction.name);
        }
    }
    return {
        text: `学分数${totalGotCredits}/${totalPlanCredits}，完成的方向：${passedDirections.join(", ")}`,
        passed: totalGotCredits >= totalPlanCredits
    }
}

const getElectiveCategoryDesc = (courses) => {
    let totalPlanCredits = plan.value.planConfigs.electiveCredits;
    let totalGotCredits = 0;
    for(let item of courses){
        if(compareGrade(item?.grade, { courseMark: 60 }) >= 0) {
            totalGotCredits += Number(item.courseCredit);
        }
    }
    return {
        text: `学分数${totalGotCredits}/${totalPlanCredits}`,
        passed: totalGotCredits >= totalPlanCredits
    }
}

</script>


<style scoped lang="scss">
.resultOuter {
    height: calc(100vh - 63px) !important;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;

    .content {}
}
</style>