<template>
    <div class="homeOuter">
        <n-h1>统计结果将开始下载，请稍候</n-h1>
        <n-modal v-model:show="showConfigModal" role="dialog" :closable="false" preset="card" style="width: 40vw;"
            :mask-closable="false" :close-on-esc="false">
            <template #header>
                <div>参数设置</div>
            </template>
            <n-form-item label="重复选修时">
                <n-radio-group v-model:value="repeatMode" name="radiobuttongroup1" style="margin-left: 5px;">
                    <n-radio-button v-for="item in ['取最高成绩', '取最初成绩']" :key="item" :value="item" :label="item" />
                </n-radio-group>
            </n-form-item>
            <n-button type="primary" @click="handleStartTask()" style="margin-top: 10px;">
                开始统计
            </n-button>
        </n-modal>
    </div>
</template>

<script setup>
import { Add, ArchiveOutline as ArchiveIcon } from '@vicons/ionicons5'
import { ref, onMounted } from "vue"
import { useRouter } from "vue-router"
import PlanApi from "@/apis/PlanApi"
import CourseApi from "@/apis/CourseApi"
import { useMessage } from "naive-ui"
import moment from "moment";
import TaskApi from '@/apis/TaskApi'

window.$message = useMessage();

const router = useRouter();

const showConfigModal = ref(true);

const repeatMode = ref("取最初成绩");

const handleStartTask = async () => {
    showConfigModal.value = false;
    await TaskApi.getGradeStatistics({
        taskId: router.currentRoute.value.query.taskId,
        planId: router.currentRoute.value.query.planId,
        repeatMode: repeatMode.value
    })
    window.open($baseUrl + "/result.xlsx")
}

</script>


