<template>
    <div class="plan">
        <div class="buttons">
            <n-button type="info" size="large" @click="showNewPlanModal = true">
                <template #icon>
                    <n-icon>
                        <Add />
                    </n-icon>
                </template>
                新建教学计划
            </n-button>
        </div>
        <n-table style="margin-top: 20px;">
            <thead>
                <tr>
                    <th>教学计划名称</th>
                    <th>创建时间</th>
                    <th>修改时间</th>
                    <th>操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in plans">
                    <td>{{ item.planName }}</td>
                    <td>{{ moment(item.createdAt).format("YYYY-MM-DD hh:mm:ss") }}</td>
                    <td>{{ moment(item.updatedAt).format("YYYY-MM-DD hh:mm:ss") }}</td>
                    <td>
                        <n-button @click="handleEditPlan(item)">
                            编辑
                        </n-button>
                        <n-popconfirm @positive-click="handleDeletePlan(item.planId)" positive-text="确认"
                            negative-text="取消">

                            <template #trigger>
                                <n-button type="error" style="margin-left: 10px;">
                                    删除
                                </n-button>
                            </template>
                            确认删除吗
                        </n-popconfirm>
                    </td>
                </tr>
            </tbody>
        </n-table>
        <n-modal v-model:show="showNewPlanModal" preset="card" style="width: 40vw;" :mask-closable="false">

            <template #header>
                <div>新建教学计划</div>
            </template>
            <n-form-item label="教学计划">
                <n-input v-model:value="newForm.planName" type="text" placeholder="教学计划"/>
            </n-form-item>
            <n-button type="primary" @click="handleCreatePlan()" style="margin-top: 10px;">
                确定
            </n-button>
        </n-modal>
    </div>
</template>

<script setup>
import { Add } from '@vicons/ionicons5'
import { ref, onMounted } from "vue"
import { useRouter } from "vue-router"
import PlanApi from "@/apis/PlanApi"
import { useMessage } from "naive-ui"
import moment from "moment";

window.$message = useMessage();

const router = useRouter();

const showNewPlanModal = ref(false);

const plans = ref([]);

onMounted(async () => {
    await reloadData();
})

const reloadData = async () => {
    plans.value = await PlanApi.getAllPlans();
}

const handleEditPlan = async (item) => {
    router.push({name:"planDetail", query:{planId: item.planId}})
}

const handleDeletePlan = async (planId) => {
    let result = await PlanApi.deletePlan({ planId });
    reloadData();
    window.$message.success("删除成功");
}

const newForm = ref({
    planName: ""
})

const handleCreatePlan = async () => {
    await PlanApi.createPlan(newForm.value);
    await reloadData();
    showNewPlanModal.value = false;
    window.$message.success("创建成功");
}

</script>

<style lang="scss" scoped>
.plan {
    padding: 20px;
}
</style>@/apis/PlanApi