import axios from "axios"
export default{
    async getStudentsList(params){
        let result = await axios({
            method: 'get',
            url:  $baseUrl + '/tasks/getStudentsList',
            params
        });
        return result?.data?.data;
    },
    async getDataByStudentId(params){
        let result = await axios({
            method: 'get',
            url:  $baseUrl + '/tasks/getDataByStudentId',
            params
        });
        return result?.data?.data;
    },
    async getGradeStatistics(params){
        let result = await axios({
            method: 'get',
            url:  $baseUrl + '/tasks/getGradeStatistics',
            params
        });
        return result?.data?.data;
    }
}