<template>
    <div class="planDetail">
        <n-page-header :subtitle="'最后修改时间：' + moment(plan.createdAt).format('YYYY-MM-DD hh:mm:ss')" @back="handleBack">
            <template #title>
                {{ plan.planName }}
            </template>

            <template #extra>
                <n-space>
                    <n-button size="large" type="info" @click="handleStartEditPlan">重命名</n-button>
                    <n-popconfirm @positive-click="handleDeletePlan()" positive-text="确认" negative-text="取消">

                        <template #trigger>
                            <n-button size="large" type="error">删除</n-button>
                        </template>
                        确认删除吗
                    </n-popconfirm>
                </n-space>
            </template>

            <n-card title="方案课程" hoverable style="margin-bottom: 20px;">
                <div class="buttons">
                    <div class="left">
                        <n-button type="info" @click="showNewCourseModal = true">
                            <template #icon>
                                <n-icon>
                                    <Add />
                                </n-icon>
                            </template>
                            新建课程
                        </n-button>
                    </div>
                    <div class="right">
                        <n-button type="success" @click="showImportCourseModal = true">
                            通过 Excel 导入
                        </n-button>
                    </div>
                </div>
                <n-table style="margin-top: 20px;">
                    <thead>
                        <tr>
                            <th>课程代码</th>
                            <th>课程名称</th>
                            <th>课程模块</th>
                            <th>学分</th>
                            <th>创建时间</th>
                            <th>修改时间</th>
                            <th>操作</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in courses">
                            <td>{{ item.courseCode }}</td>
                            <td>{{ item.courseName }}</td>
                            <td>{{ item.courseCategory }}</td>
                            <td>{{ item.courseCredit }}</td>
                            <td>{{ moment(item.createdAt).format("YYYY-MM-DD hh:mm:ss") }}</td>
                            <td>{{ moment(item.updatedAt).format("YYYY-MM-DD hh:mm:ss") }}</td>
                            <td>
                                <n-button @click="handleStartEditCourse(item)">
                                    编辑
                                </n-button>
                                <n-popconfirm @positive-click="handleDeleteCourse(item.courseId)" positive-text="确认"
                                    negative-text="取消">

                                    <template #trigger>
                                        <n-button type="error" style="margin-left: 10px;">
                                            删除
                                        </n-button>
                                    </template>
                                    确认删除吗
                                </n-popconfirm>
                            </td>
                        </tr>
                    </tbody>
                </n-table>
            </n-card>
            <n-card title="学分要求" hoverable style="margin-bottom: 20px;">
                <div class="buttons">
                    <div class="left">
                        当前方案：通识教育选修课程要求至少达到 {{ plan?.planConfigs?.electiveCredits }} 学分方可毕业，
                        专业选修课程要求至少达到 {{ plan?.planConfigs?.profElectiveCredits }} 学分方可毕业
                    </div>
                    <div class="right">
                        <n-button type="success" @click="startEditPlanConfig(); showEditElectiveModal = true;">
                            编辑
                        </n-button>
                    </div>
                </div>
            </n-card>
            <n-card title="专业选修课方向" hoverable style="margin-bottom: 20px;">
                <div class="buttons">
                    <div class="left">
                        <n-button type="info" @click="addNewDirection">
                            添加方向
                        </n-button>
                    </div>
                    <div class="right">
                        <n-button type="success" @click="handleSaveDirections">
                            保存更改
                        </n-button>
                    </div>
                </div>

                <div class="directions" style="margin-top: 20px;">
                    <div style="display: flex; justify-content: space-between; margin-bottom: 10px;" v-for="(direction, index) in plan?.planConfigs?.directions">
                        <div class="left" style="display: flex;">
                            <n-input v-model:value="direction.name" autosize style="min-width: 200px;" placeholder="输入选修方向名"></n-input>
                            <n-select v-model:value="direction.courses" multiple :options="courses" label-field="courseName" value-field="courseCode"
                            style="margin-left: 10px; min-width: 200px;" placeholder="选择方向课程"/>
                        </div>
                        <div class="right">
                            <n-popconfirm @positive-click="plan?.planConfigs?.directions.splice(index, 1)" positive-text="确认"
                                negative-text="取消">
                                <template #trigger>
                                    <n-button type="error" style="margin-left: 10px;">
                                        删除
                                    </n-button>
                                </template>
                                确认删除吗
                            </n-popconfirm>
                        </div>
                    </div>
                </div>
            </n-card>
        </n-page-header>
        <n-modal v-model:show="showEditPlanModal" preset="card" style="width: 40vw;" :mask-closable="false">

            <template #header>
                <div>重命名教学计划</div>
            </template>
            <n-form-item label="教学计划名称">
                <n-input v-model:value="editForm.planName" type="text" placeholder="教学计划名称" />
            </n-form-item>
            <n-button type="primary" @click="handleEditPlan()" style="margin-top: 10px;">
                确定
            </n-button>
        </n-modal>
        <n-modal v-model:show="showNewCourseModal" preset="card" style="width: 40vw;" :mask-closable="false">

            <template #header>
                <div>新建课程</div>
            </template>
            <n-form-item label="课程代码">
                <n-input v-model:value="newCourseForm.courseCode" type="text" placeholder="课程代码" />
            </n-form-item>
            <n-form-item label="课程名">
                <n-input v-model:value="newCourseForm.courseName" type="text" placeholder="课程名" />
            </n-form-item>
            <n-form-item label="课程模块">
                <n-select tag filterable v-model:value="newCourseForm.courseCategory"
                    :options="courseCategorySelections" placeholder="课程模块，此项既可选择，也可输入自定义值" />
            </n-form-item>
            <n-form-item label="学分">
                <n-input-number :precision="1" placeholder="学分" v-model:value="newCourseForm.courseCredit" />
            </n-form-item>
            <n-button type="primary" @click="handleCreateCourse()" style="margin-top: 10px;">
                确定
            </n-button>
        </n-modal>
        <n-modal v-model:show="showEditCourseModal" preset="card" style="width: 40vw;" :mask-closable="false">

            <template #header>
                <div>编辑课程</div>
            </template>
            <n-form-item label="课程代码">
                <n-input v-model:value="editCourseForm.courseCode" type="text" placeholder="课程代码" />
            </n-form-item>
            <n-form-item label="课程名">
                <n-input v-model:value="editCourseForm.courseName" type="text" placeholder="课程名" />
            </n-form-item>
            <n-form-item label="课程模块">
                <n-select tag filterable v-model:value="editCourseForm.courseCategory"
                    :options="courseCategorySelections" placeholder="课程模块，此项既可选择，也可输入自定义值" />
            </n-form-item>
            <n-form-item label="学分">
                <n-input-number :precision="1" placeholder="学分" v-model:value="editCourseForm.courseCredit" />
            </n-form-item>
            <n-button type="primary" @click="handleEditCourse()" style="margin-top: 10px;">
                确定
            </n-button>
        </n-modal>
        <n-modal v-model:show="showImportCourseModal" preset="card" style="width: 50vw;" :mask-closable="false">

            <template #header>
                <div>通过Excel导入课程</div>
            </template>
            <n-upload :action="baseUrl + '/courses/importByExcel'"
                :data="{ planId: router.currentRoute.value.query.planId }" @before-upload="beforeUpload"
                :on-finish="onFinishUpload" :show-file-list="false" :on-error="onUploadError">
                <n-upload-dragger>
                    <div style="margin-bottom: 12px">
                        <n-icon size="48" :depth="3">
                            <archive-icon />
                        </n-icon>
                    </div>
                    <n-text style="font-size: 16px">
                        点击或者拖动文件到该区域来导入
                    </n-text>
                    <n-p depth="3" style="margin: 8px 0 0 0">
                        导入的文件应符合格式要求，至少包含以下几列：课程模块、课程代码、课程名称、学分。
                    </n-p>
                </n-upload-dragger>
            </n-upload>
            <n-button size="small" secondary strong style="margin-top: 10px; float: right;" @click="downloadTemplate">
                点击此处下载模板文件
            </n-button>
        </n-modal>
        <n-modal v-model:show="showEditElectiveModal" preset="card" style="width: 40vw;" :mask-closable="false">

            <template #header>
                <div>编辑学分要求</div>
            </template>
            <n-form-item label="公选课学分">
                <n-input-number :precision="1" placeholder="公选课学分" v-model:value="editPlanConfigsForm.electiveCredits" />
            </n-form-item>
            <n-form-item label="专业选修课学分">
                <n-input-number :precision="1" placeholder="专业选修课学分" v-model:value="editPlanConfigsForm.profElectiveCredits" />
            </n-form-item>
            <n-button type="primary" @click="handleEditPlanConfig()" style="margin-top: 10px;">
                确定
            </n-button>
        </n-modal>
    </div>
</template>

<script setup>
import { Add, ArchiveOutline as ArchiveIcon } from '@vicons/ionicons5'
import { ref, onMounted } from "vue"
import { useRouter } from "vue-router"
import PlanApi from "@/apis/PlanApi"
import CourseApi from "@/apis/CourseApi"
import { useMessage } from "naive-ui"
import moment from "moment";

window.$message = useMessage();

const baseUrl = ref("");

const router = useRouter();

const courses = ref([]);

const plan = ref({});

onMounted(async () => {
    await reloadData();
    baseUrl.value = $baseUrl;
})

const handleBack = () => {
    router.replace({ name: "plans" });
}

const reloadData = async () => {
    plan.value = await PlanApi.getPlanById({ planId: router.currentRoute.value.query.planId })
    plan.value.planConfigs = JSON.parse(plan.value.planConfigs);
    courses.value = await CourseApi.getCoursesByPlanId({ planId: router.currentRoute.value.query.planId });
}

const handleDeletePlan = async () => {
    let result = await PlanApi.deletePlan({ planId: plan.value.planId });
    router.replace({ name: "plans" });
    window.$message.success("删除成功");
}

// 重命名教学方案
const editForm = ref({
    planName: ""
})

const showEditPlanModal = ref(false);

const handleStartEditPlan = async () => {
    editForm.value.planName = plan.value.planName;
    showEditPlanModal.value = true;
}

const handleEditPlan = async () => {
    await PlanApi.editPlan({ ...editForm.value, planId: plan.value.planId });
    await reloadData();
    showEditPlanModal.value = false;
    window.$message.success("修改成功");
}

const courseCategorySelections = [
    {
        value: "通识教育必修课",
        label: "通识教育必修课"
    },
    {
        value: "公共基础课程",
        label: "公共基础课程"
    },
    {
        value: "专业必修课程",
        label: "专业必修课程"
    },
    {
        value: "专业选修课程",
        label: "专业选修课程"
    },
    {
        value: "通识教育选修课",
        label: "通识教育选修课"
    },
    {
        value: "其他必修课程",
        label: "其他必修课程"
    }
]

// 新建课程
const newCourseForm = ref({
    planId: router.currentRoute.value.query.planId
});

const showNewCourseModal = ref(false);


const handleCreateCourse = async () => {
    await CourseApi.createCourse(newCourseForm.value);
    await reloadData();
    showNewCourseModal.value = false;
    window.$message.success("创建成功");
}


// 编辑课程
const showEditCourseModal = ref(false);

const editCourseForm = ref({});

const handleStartEditCourse = async (item) => {
    editCourseForm.value = JSON.parse(JSON.stringify(item));
    showEditCourseModal.value = true;
}

const handleEditCourse = async () => {
    await CourseApi.editCourse(editCourseForm.value);
    await reloadData();
    showEditCourseModal.value = false;
    window.$message.success("修改成功");
}


// 删除课程
const handleDeleteCourse = async (courseId) => {
    let result = await CourseApi.deleteCourse({ courseId: courseId });
    await reloadData();
    window.$message.success("删除成功");
}

// 导入课程
const showImportCourseModal = ref(false);

const beforeUpload = async (data) => {
    if (data.file.name.indexOf(".xlsx") == -1 && data.file.name.indexOf(".xls") == -1) {
        window.$message.error("只能上传Excel表格文件，请重新上传");
        return false;
    }
    return true;
}

const onFinishUpload = async (data) => {
    showImportCourseModal.value = false;
    await reloadData();
    window.$message.success("导入成功");
}


const onUploadError = async (data) => {
    window.$message.error(JSON.parse(data.event.currentTarget.response).data);
}


const editPlanConfigsForm = ref({});

// 公选课学分编辑
const showEditElectiveModal = ref(false);

const startEditPlanConfig = () => {
    showEditElectiveModal.value = true;
    editPlanConfigsForm.value = JSON.parse(JSON.stringify(plan.value.planConfigs));
}

const handleEditPlanConfig = async () => {
    await PlanApi.editPlan({
        planId: plan.value.planId,
        planConfigs: JSON.stringify(editPlanConfigsForm.value)
    });
    await reloadData();
    showEditElectiveModal.value = false;
    window.$message.success("修改成功");
}

// 方向课编辑

const addNewDirection = async () => {
    if (plan.value.planConfigs.directions == undefined) {
        plan.value.planConfigs.directions = [];
    }
    plan.value.planConfigs.directions.push({
        name: "",
        courses: []
    })
}

const handleSaveDirections = async () => {
    await PlanApi.editPlan({
        planId: plan.value.planId,
        planConfigs: JSON.stringify(plan.value.planConfigs)
    });
    await reloadData();
    showEditElectiveModal.value = false;
    window.$message.success("修改成功");
}

const downloadTemplate = () =>{
    window.open($baseUrl + "/教学计划模板文件.xlsx")
}

</script>

<style lang="scss" scoped>
.planDetail {
    padding: 20px;

    .buttons {
        display: flex;
        justify-content: space-between;
    }

}
</style>@/apis/PlanApi