import axios from "axios"
export default{
    async getAllPlans(){
        let result = await axios({
            method: 'get',
            url:  $baseUrl + '/plans/getAllPlans',
        });
        return result?.data?.data;
    },
    async getPlanById(data){
        let result = await axios({
            method: 'post',
            url:  $baseUrl + '/plans/getPlanById',
            data
        });
        return result?.data?.data;
    },
    async createPlan(data){
        let result = await axios({
            method: 'post',
            url:  $baseUrl + '/plans/createPlan',
            data
        });
    },
    async editPlan(data){
        let result = await axios({
            method: 'post',
            url:  $baseUrl + '/plans/editPlan',
            data
        });
    },
    async deletePlan(data){
        let result = await axios({
            method: 'post',
            url:  $baseUrl + '/plans/deletePlan',
            data
        });
    },
}